@import 'components/styles/import.scss';

.cheat-window {
  background: white;
  padding: 16px;

  .label-numberbox-button {
    display: flex;
    padding: 2px;
  }

  .label-numberbox-button label {
    flex: 1;
    padding-left: 2px;
  }

  .label-numberbox-button button {
    height: 23px;
    margin-left: 4px;
  }

  .label-dropdown {
    display: flex;
    padding: 2px;
  }

  .label-dropdown label {
    flex: 1;
    padding-left: 2px;
  }


  .item-group {
    display: flex;

    .item-group-label {
      flex: 1
    }
  }

  .item-option {
    display: flex;
    height: 24px;
    overflow: hidden;

    .item-icon {
      margin-right: 6px;
    }

    .item-label {
      flex: 1;
      padding-top: 2px;

      span {
        height: 24px;
        padding-left: 8px;
        color: $color-text-darker;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }
}
